.SearchBar {
  text-align: center;
}

.SearchBar .autocompleteSuggestions {
  max-height: 90%;
  overflow-y: hidden;
  scrollbar-width: thin;
}

.SearchBar .autocompleteSuggestion {
  width: 100%;
  padding: 8px 0px;
  background: #fff;
}

.SearchBar .autocompleteSuggestion p {
  margin: 0px;
  text-align: left;
  padding: 4px;
}

.SearchBar .autocompleteSuggestion:hover {
  width: 100%;
  background: #eee;
  cursor: pointer;
  transition: ease .3s;
}

.SearchBar input {
  border-radius: 3px;
  border: 1px solid #ddd;
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  font-family: inherit;
}