.NavBar {
  position: fixed;
  top: 0px;
  z-index: 100;
  background: #304865;
  color: white;
  box-shadow: 0px 4px 4px 0px rgba(0,0,0,.8);
}

.NavBar .Title {
  display: inline-block;
  margin: 12px 0px 10px 120px;
  cursor: default;
}

.NavBar h2 {
  padding: 0px;
  margin: 0px;
}

.NavBar .SearchBarContainer {
  position: fixed;
  left: 280px;
  top: 0px;
  margin: 7px 0px 10px 120px;
}

.NavBar .SearchBar .autocompleteSuggestion {
  background: #304865;
}

.NavBar .SearchBar .autocompleteSuggestion:hover {
  background: #455A74;
}

.NavBar .ProfileContainer {
  float: right;
  margin: 4px 120px 4px 0px;
}

.NavBar .ProfileIcons {
  padding: 3px;
  border-radius: 20px;
  border: 2px solid white;
}

.NavBar .ProfileIcons:hover {
  cursor: pointer;
  box-shadow: 4px 4px 4px 0px rgba(0,0,0,.8);
  transition: ease .2s;
}

.NavBar .ProfileSections {
  width: 120%;
  padding: 8px 0px;
  margin: 7px -15px;
  border-radius: 15px;
  background: #304865;
  box-shadow: 4px 4px 4px 0px rgba(0,0,0,.8);
}

.NavBar .ProfileSection {
  width: 100%;
  border-radius: 2px;
}

.NavBar .ProfileSection:hover {
  background: #455A74;
  cursor: pointer;
  transition: ease .4s;
}

.NavBar .ProfileSection p {
  margin: 0px;
  text-align: right;
  padding: 4px 8px;
}